import React from 'react'
import { Layout, Stack, Main, Sidebar } from '@layout'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Reveal from '@components/Reveal'
import Sticky from '@components/Sticky'
import Seo from '@widgets/Seo'
import Tags from '@widgets/Tags'
import Sponsor from '@widgets/Sponsor'
import Social from '@widgets/Social'
import NewsletterCompact from '@widgets/NewsletterCompact'
import Categories from '@widgets/Categories'
import { useBlogTags, useBlogCategories } from '@helpers-blog'
// import useSiteMetadata from '@qiyo_helpers/useMySiteMetadata'

// Michael Lu, 2024/05/10
import CookieAlert from '@qiyo_components/cookiealert/CookieAlert'
import UsageAlert from '@qiyo_components/usagealert/UsageAlert'

const Posts = ({
  data: { posts = {}, featuredPosts = {}, recentPosts = {} },
  ...props
}) => {
  const { pageContext: { services = {} } = {} } = props
  const tags = useBlogTags()
  const categories = useBlogCategories()
  // const { startPostDate } = useSiteMetadata()

  // console.log('Posts startPostDate: ', startPostDate)

  function test(arg)
  {
    // console.log('Posts TEST: ', arg)
    return true
  }

  function sortByCatOrder(a, b)
  {
    let catNameA = a.categoryName
    let catNameB = b.categoryName
    let orderA = categories.find((cat)=>cat.name===catNameA).catOrder
    let orderB = categories.find((cat)=>cat.name===catNameB).catOrder
    // 升序
    if(orderA > orderB){
      return 1; // 正數時，後面的數放在前面
    } else {
      return -1 // 負數時，前面的數放在前面
    }
  }

  // console.log('Posts TEST')

  return (
    <>
      <Layout {...props}>
        <Seo title='首頁' cacheControls='no-cache, no-store, must-revalidate' />
        <Divider />
        <Stack effectProps={{ effect: false }}>
          <Main>
            <CardList
              nodes={featuredPosts.nodes}
              variant='vertical-cover'
              limit={4}
              columns={[1, 2]}
              title='焦點'
              slider
              autoPlay
              loading='eager'
            />
            <Divider />
            <CardList
              nodes={featuredPosts.nodes}
              variant={['horizontal-md', 'horizontal']}
              limit={2}
              skip={4}
              title='關注'
              loading='eager'
            />
            {posts.group &&
              posts.group.sort(sortByCatOrder).map(group => test(group) && (
                <>
                  <Divider />
                  <Reveal>
                    <CardList
                      nodes={group.nodes}
                      variant={['horizontal-md', 'vertical']}
                      limit={2}
                      columns={[1, 2, 2, 2]}
                      title={group.categoryName}
                      omitCategory
                      withTitleLink
                    />
                    <Divider space={2} />
                    <CardList
                      nodes={group.nodes}
                      variant={['horizontal-md', 'horizontal-aside']}
                      limit={4}
                      skip={2}
                      columns={[1, 2, 2, 2]}
                      omitCategory
                      withTitleLink
                    />
                  </Reveal>
                </>
              ))}
          </Main>
          <Sidebar>
            <CardList
              nodes={recentPosts.nodes}
              limit={4}
              variant='horizontal-aside'
              title='最近更新'
              omitMedia
              omitCategory
              aside
            />
            <Divider />

            {false && <Sponsor />}
            {false && <Divider />}

            {false && <Social />}
            {false && <Divider />}

            <Tags tags={tags} />
            <Divider />

            <Sticky>
              <Categories categories={categories} />
              {services.mailchimp && (
                <>
                  <Divider />
                  <NewsletterCompact />
                </>
              )}
            </Sticky>
          </Sidebar>
        </Stack>
      </Layout>

      <CookieAlert></CookieAlert>
      <UsageAlert></UsageAlert>
    </>
  )
}

export default Posts
