import React from 'react'
import { Box } from 'theme-ui'
import { FaArchive } from 'react-icons/fa'
import IconButton from '@components/IconButton'
import Section from '@components/Section'

// free icons
// https://fontawesome.com/search?q=chart&o=r&m=free

const styles = {
  horizontal: {
    display: `flex`,
    flexWrap: `nowrap`,
    overflowX: `auto`,
    width: `auto`,
    scrollBehavior: `smooth`,
    m: -2,
    a: {
      flex: 1,
      minWidth: [`1/3`, `auto`],
      m: 2
    }
  }
}

function sortByCatOrder(a, b)
{
  let orderA = a.catOrder
  let orderB = b.catOrder
  // 升序
  if(orderA > orderB){
    return 1; // 正數時，後面的數放在前面
  } else {
    return -1 // 負數時，前面的數放在前面
  }
}

const Categories = ({ variant, categories, ...props }) => (
  <Section aside={variant === 'vertical'} title='主題' {...props}>
    <Box sx={styles[variant]}>
      {categories &&
        categories.sort(sortByCatOrder).map(({ id, name, slug, totalCount, icon, hideCat }) => {
          const buttonProps = {
            key: id,
            name,
            number: totalCount,
            to: slug,
            iconPath: icon,
            Icon: !icon && FaArchive,
            variant
          }

          return hideCat !== "true" && totalCount !== 0 && <IconButton {...buttonProps} />
        })}
    </Box>
  </Section>
)

export default Categories

Categories.defaultProps = {
  variant: 'vertical'
}
